.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3.125rem 0;
}
.nav #icon {
  width: 2.1875rem;
  cursor: pointer;
  margin: 0.625rem 0;
}
.nav .dev-name {
  color: var(--site-name);
  font-size: 1.375rem;
  cursor: pointer;
  font-weight: 700;
}
.nav .nav__items {
  display: flex;
  justify-content: center;
  padding: 0;
  gap: 1.5625rem;
  position: relative;
}
.nav .nav__items .nav__item {
  list-style: none;
}
.nav .nav__items .nav__link {
  position: relative;
  color: var(--text-primary);
  font-weight: 500;
  font-size: 1.125rem;
  padding-bottom: 0.3125rem;
}
.nav .nav__items .nav__link-active {
  color: var(--active-link);
}
.nav .nav__items .nav__marker {
  position: absolute;
  left: 0;
  bottom: 0;
  height: 0.1875rem;
  width: 0;
  background-color: var(--active-link);
  transition: all 500ms;
  border-radius: 0.625rem;
}

:root {
  --selected-bg: hsl(52, 100%, 50%);
  --selected-text: hsl(240, 5%, 22%);
  --app-background: hsl(0, 0%, 100%);
  --site-name: hsl(211, 100%, 50%);
  --text-primary: hsl(240, 9%, 29%);
  --active-link: hsl(167, 100%, 26%);
}

body.night {
  --app-background: hsl(222, 27%, 12%);
  --text-primary: hsl(240, 11%, 72%);
  --active-link: hsl(167, 100%, 33%);
}

html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

*,
:after,
:before {
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}

::-moz-selection {
  background: var(--selected-bg);
}

::selection {
  background: var(--selected-bg);
}

body,
html {
  margin: 0;
  padding: 0;
  background-color: var(--app-background);
  color: var(--text-primary);
  font-family: "Inter", sans-serif;
  line-height: 1.5;
  width: 100%;
}

body.night ::-moz-selection,
html.night ::-moz-selection {
  color: var(--selected-text);
  background: var(--selected-bg);
}

body.night ::selection,
html.night ::selection {
  color: var(--selected-text);
  background: var(--selected-bg);
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
  outline: none;
  border: none;
}

.container {
  width: 100%;
  padding: 0 clamp(1.375rem, 1.2rem + 0.89vw, 2rem);
  max-width: 70rem;
  margin: 0 auto;
}
@media (min-width: 70em) {
  .container {
    padding: 0;
  }
}